@charset "UTF-8";

/* ========== Header.jsx : 헤더 ========== */
/* 기본 레이아웃 */
.header {
    z-index: 98;
    width: 80vw;
    box-sizing: border-box;
    padding: 30px 50px;
    position: fixed;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 20px auto;
}

/* 불투명한 유리 효과 + 노이즈 필터 */
.glassmorphism {
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);  /* 배경 흐림 효과 */
    -webkit-backdrop-filter: blur(5px);  /* 사파리 호환 */
    border-radius: 50px;
    background-image: url(data:image/png;base64,);
}

/* 헤더 로고 */
.header__logo {
    color: white;
    font: 1.8rem/1.8rem "Rubik Bubbles";
}

/* nav__item 배치 : grid, 5열 */
.nav__list {
    display: grid;
    grid-template-columns: repeat(5, max-content);
    column-gap: 3.2rem;;
    color: white;
    line-height: 1.8rem;
    font: 17px "Avenir";
    letter-spacing: 0.5px;
}

/* 메뉴 */
.nav__item {
    cursor: pointer;
}